<template>
  <div class="carousel">
    <el-carousel>
      <el-carousel-item v-for="(img, index) in imgArray"
                        :key="index">
        <a href="#">
          <img :src='img'>
        </a>
      </el-carousel-item>
    </el-carousel>
     <!-- <img :src='img'> -->
  </div>
</template>

<script>

export default {
  name: 'tabBar',
  data () {
    return {
      imgArray: ['/static/banner1.png', '/static/banner2.png']
      // img:'/static/banner.png'
    }
  },
  components: {
  },
  watch: {
  },
  created () {
  },
  methods: {

  },
  mounted () { }
}
</script>

<style  scoped>
.carousel,img{
  width: 100%;
  margin: 0 auto;
}
</style>



