import Vue from 'vue'
import Router from 'vue-router'
import SaleList from "../components/SaleList/SaleList.vue";
import GoodsDetail from "../components/GoodsDetail/GoodsDetail.vue";
import Purchase from "../components/Purchase/Purchase.vue";
import PurchaseDetail from "../components/PurchaseDetail/PurchaseDetail.vue";
import home from '../components/home.vue'
import hall from '../components/hall.vue'
Vue.use(Router)

export default new Router({
  routes: [{
      path: "/",
      // redirect: "/home"
      component: home,
    },
    {
      path: "/home",
      name: 'home',
      component: home,
    },
    {
      path: "/hall",
      name: 'hall',
      component: hall,
    },
    {
      path: "/salelist",
      name: 'salelist',
      component: SaleList,
    },
    {
      path: "/purchase",
      name: 'purchase',
      component: Purchase,
    },
    {
      path: "/purchasedetail",
      name: 'purchasedetail',
      component: PurchaseDetail,
    },
    {
      path: "/goodsdetail",
      name: 'goodsdetail',
      component: GoodsDetail,
    },
  ]
})



