<template>
  <div class="sale clearfix w1180">
    <div class="fl nav">
      <router-link :to="{path:'/salelist',query: {infoType: '9',oilType:'9'}}">
        <ul>
          <li class="active">销售</li>
          <li>优质客户认证</li>
          <li>精准匹配，更多商机</li>
        </ul>
      </router-link>
    </div>
    <div class="fr container">
      <p class="new">
        <a href="#/salelist?infoType=9&oilType=9"
           class="fr">查看更多</a>最新推荐</p>
      <ul class="clearfix">
        <li v-for="(val,index) in datas"
            :key="index">
          <router-link :to="{ path: 'goodsdetail', query: { uuid:val.UUID}}">
            <div class="con">
              <p class="company nowrap">{{val.EP_MEMBER_NAME}}</p>
              <p class="place nowrap">{{val.LOCATION_PROVINCE}} / {{val.LOCATION_CITY}}</p>
              <p class="oil nowrap">{{val.OIL_CLASSIFICATION}} {{val.OIL_VARIETIES}}</p>
              <p class="unit nowrap">{{val.UNIT_PRICE}}元/{{val.UNIT}}</p>
            </div>
            <em class="type">{{val.DELIVERY_WAY}}</em>
            <em class="type2"
                :class="{'bg': val.TRANSPORT_WAY=='配送'}"></em>
          </router-link>
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'saleRaw',
  data () {
    return {
      datas: {},
    }
  },
  components: {
  },
  watch: {
  },
  created () {
  },
  methods: {
    getData: function () {
      this.ajax(url.home1, { "infoType": 9,status : "1,2"}, 'post').then((res) => {
        if (!!res.data.datas && res.data.datas.length !== 0) {
          this.datas = res.data.datas.slice(0, 6)
        }
      })
    }
  },
  mounted () {
    this.getData()
  }
}
</script >

<style scoped>
.nav {
  width: 240px;
  height: 540px;
  background: url('/static/list1.png') no-repeat center center;
  opacity: 0.8;
  margin-bottom: 30px;
}
.nav ul {
  padding: 386px 0 0 20px;
}
.nav ul li {
  line-height: 42px;
  height: 42px;
  font-size: 16px;
  color: #ffffff;
}
.active {
  font-size: 24px !important;
}
.container {
  width: 939px;
  height: 538px;
  margin-bottom: 30px;
  border: 1px solid #e8e8e8;
  border-left: none;
}
.new {
  font-size: 18px;
  color: rgba(51, 51, 51, 1);
  padding: 18px 20px 15px 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #e8e8e8;
    font-weight: bold;
}
.new a {
  font-size: 14px;
  color: rgba(51, 51, 51, 1);
  line-height: 24px;
  position: relative;
  padding-right: 16px;
  font-weight:normal;
}
.new a:after {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-bottom: 1px solid #000;
  border-left: 1px solid #000;
  transform: rotate(-135deg);
  position: absolute;
  top: 9px;
  right: 4px;
}
.container ul {
  padding-left: 30px;
}
.container li {
  width: 280px;
  height: 200px;
  position: relative;
  float: left;
  margin-right: 20px;
  margin-bottom: 20px;
  border: 1px solid #e8e8e8;
}
.container li img {
  width: 56px;
  height: 56px;
  position: absolute;
  left: 20px;
  top: 20px;
}
.con {
  padding-top: 24px;
  text-align: center;
  color: rgba(51, 51, 51, 1);
}
.type {
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: rgba(153, 153, 153, 1);
  line-height: 18px;
}
.bg {
  background: url('/static/bg2.png') no-repeat center center !important;
}
.type2 {
  position: absolute;
  bottom: 10px;
  left: 0px;
  width: 53px;
  height: 24px;
  background: url('/static/bg1.png') no-repeat center center;
  border-radius: 0px 30px 30px 0px;
  font-size: 12px;
  color: #fff;
  line-height: 24px;
  text-align: center;
}
.unit {
  font-size: 18px;
  color: rgba(243, 101, 35, 1);
  line-height: 24px;
}
.oil {
  font-size: 18px;
  line-height: 24px;
  padding-bottom: 12px;
}
.place {
  line-height: 18px;
  padding-bottom: 10px;
  margin: 0 22px 15px;
  border-bottom: 1px solid #e8e8e8;
}
.company {
  font-size: 16px;
  line-height: 20px;
  padding-bottom: 10px;
}
</style>



