          con1: '上海英斯贝克商品检验有限公司于1997年05月06日在上海市工商局登记成立。公司经营范围包括涉及矿产品，金属材料，煤和焦炭，石油化工产品，消费品，农产品，化肥等商品。从事进出口商品检验、品质分析、相关产品认证。主要业务涵盖金属、矿产、石化、农产品等各类产品。',
