          title: '中化泉州石化有限公司',
          con1: '中化泉州石化有限公司隶属于中国中化集团公司，项目由国有大型骨干企业、世界五百强企业中国中化集团投资建设，选址于福建省泉州湾石化基地泉惠石化工业园区，占地约4000亩。',
