          con2: '特别是在加盟中国中化集团公司之后，使公司迈上了前所未有的发展快车道。销售产品：93#国四汽油，93#组分汽油（国四），97#汽油（国四），0#、-10#、-20#加氢柴油，70#A、90#A级沥青，石油焦，低硫加氢石脑油，丙烯、丙烷、液化气，硫磺，油浆等产品。'
        },
        {
          title: 'Societe Generale de Surveillance S.A. ',
          con1: 'SGS 是 Societe Generale de Surveillance S.A. 的简称，译为“通用公证行”。其总部在瑞士的日内瓦，中国分支机构叫“通标标准技术服务有限公司”SGS是全球领先的检验、鉴定、测试和认证机构，是公认的质量和诚信的基准。',
          con2: 'SGS全名瑞士通用公证行，创建于1878年，是目前世界上最大、资格最老的民间第三方从事产品质量控制和技术鉴定的跨国公司。其总部设在瑞士的日内瓦，在世界各地设有1500多家分支机构和专业实验室和 75000多名员工（包括科研人员，工程师，博士，化学家，审核员和检验员等），在全球143个国家开展检验、鉴定、测试和认证服务。'
        },
