    var timer1 = setInterval(function () {
      thef.yuan++
      if (thef.yuan == 86) {
        clearInterval(timer1);
      }
    }, 10);
    var timer2 = setInterval(function () {
      thef.dun++
      if ( thef.dun == 190) {
        clearInterval(timer2);
      }
    }, 10);
  },
  methods: {
  },
  mounted () {
    var entries = [
      { label: 'Sinochemsg' },
      { label: '京博石化' },
      { label: 'CSETMARKET' },
      { label: 'Freepoint' },
      { label: '中化船务' },
      { label: '泉州外代' },
      { label: '兴通船代' },
      { label: 'INSPECTORATE' },
      { label: '泉州石化' },
      { label: '弘润集团' },
      { label: 'SGS' },
      { label: 'CCIC' }
    ];

    var settings = {
      entries: entries,
      width: 370,
      height: 340,
      radius: '30%',
      radiusMin: 100,
      bgDraw: true,
      bgColor: '#292929',
      opacityOver: 1.00,
      opacityOut: 0.6,
      opacitySpeed: 1,
      fov: 700,
      speed: 0.5,
      fontFamily: 'Oswald, Arial, sans-serif',
      fontSize: '18',
      fontColor: '#f36523',
      fontWeight: 'normal',//bold
      fontStyle: 'normal',//italic 
      fontStretch: 'normal',//wider, narrower, ultra-condensed, extra-condensed, condensed, semi-condensed, semi-expanded, expanded, extra-expanded, ultra-expanded
      fontToUpperCase: false
    };
    $('.right').svg3DTagCloud(settings);
  }
