  margin: 50px auto;
}
.advantage li {
  float: left;
  width: 25%;
  text-align: center;
}
.advantage li img {
  padding-bottom: 10px;
}
.bg {
  position: relative;
  color: #fff;
  font-size: 20px;
}
.bg img {
  width: 100%;
}
.yuan {
  position: absolute;
  left: 50%;
  bottom: 40px;
  margin-left: -240px;
  width: 180px;
  text-align: right;
}
.dun {
  position: absolute;
  right: 50%;
  bottom: 40px;
  margin-right: -180px;
  width: 200px;
  text-align: right;
}
.bg p span {
  font-size: 50px;
}
.bg p em {
  font-size: 14px;
  display: block;
  text-align: right;
  margin: -6px 0 0 0;
}
</style>



