<template>
  <div class="purchase clearfix w1180">
    <div class="fl nav">
      <a href="#/purchase?infoType=9&oilType=9">
        <ul>
          <li class="active">采购</li>
          <li>实力供油商认证</li>
          <li>拓宽渠道，更多选择</li>
        </ul>
      </a>
    </div>
    <div class="fr container">
      <p class="new">
        <a href="#/purchase?infoType=9&oilType=9"
           class="fr">查看更多</a>精选求购</p>
      <ul>
        <li v-for="(val,index) in datas"
            :key='index'>
          <router-link :to="{ path: 'purchasedetail', query: { uuid:val.uuid}}">
            <span class="w1">{{val.epMemberName}}</span>
            <span class="w1 tc">{{val.ypname}}   {{val.ggname}}</span>
            <span class="w2">{{val.locationProvince}}  /  {{val.locationCity}}</span>
            <span class="w2">{{val.number}}{{val.unit}}</span>
            <span class="w2">{{val.transportWay}}</span>
            <span class="w2">{{val.deliveryWay}}</span>
          </router-link>
        </li>
      </ul>

    </div>

  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'purchase',
  data () {
    return {
      datas: {}

    }
  },
  components: {
  },
  watch: {
  },
  created () {

  },
  methods: { 
    getData: function () {
      this.ajax(url.home2, {"infoType":"9",status : "1,2"}, 'post').then((res) => {
        if (!!res.data.datas && res.data.datas.length !== 0) {
          this.datas = res.data.datas.slice(0, 8)
        }
      })
    }
  },
  mounted () {
    this.getData()
  }
}
</script >

<style scoped>
.w1 {
  width: 20%
}
.w2 {
  width: 14%;
  text-align: center;
}
.tc{
  text-align: center;
}

.nav {
  width: 240px;
  height: 540px;
  background: url('/static/list2.png') no-repeat center center;
  opacity: 0.8;
  margin-bottom: 30px;
}
.nav ul {
  padding: 386px 0 0 20px;
}
.nav ul li {
  line-height: 42px;
  height: 42px;
  font-size: 16px;
  color: #ffffff;
}
.active {
  font-size: 24px !important;
}
.container {
  width: 938px;
  height: 538px;
  margin-bottom: 30px;
  border: 1px solid #ededed;
}
.new {
   font-size: 18px;
  color: rgba(51, 51, 51, 1);
  padding: 18px 20px 15px 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #e8e8e8;
    font-weight: bold;
}
.new a {
  font-size: 14px;
  color: rgba(51, 51, 51, 1);
  line-height: 24px;
  position: relative;
  padding-right: 16px;
  font-weight:normal;
}
.new a:after {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-bottom: 1px solid #000;
  border-left: 1px solid #000;
  transform: rotate(-135deg);
  position: absolute;
  top: 9px;
  right: 4px;
}
.container ul{
  padding-left: 30px
}
.container li {
  color: rgba(51, 51, 51, 1);
  line-height: 20px;
  padding: 0 0 14px 0;
  margin-bottom: 20px;
  border-bottom:1px solid #EDEDED;
}
.container li span {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



