
<template>
  <div class="saleWrap">
    <p class="smallTitle"
       v-show="history==1">
      <a :href="'#/hall?oilType=1'">船油采购 </a> >
      <span class="pointer"
            @click="refresh">采购列表</span>
    </p>
    <p class="smallTitle"
       v-show="history==9">
      <a :href="'#/hall?oilType=9'">原料油采购 </a> >
      <span class="pointer"
            @click="refresh">采购列表</span>
    </p>
    <!-- 搜索条件 -->
    <div class="searchfactirs">
      <!-- 第一列 选择时间  公司 -->
      <div class="flo firstColumn">
        <div class="searchDate">
          <div class="firstfloat block">
            <span class="demonstration">发布日期:</span>
            <el-date-picker id="startDate"
                            v-model="startDate"
                            type="date"
                            placeholder="选择日期">
            </el-date-picker>
          </div>
          <span class="firstfloat line">-</span>
          <div class="firstfloat block">
            <el-date-picker v-model="endDate"
                            id="endDate"
                            type="date"
                            placeholder="选择日期">
            </el-date-picker>
          </div>
        </div>
        <div class="companyName">
          <span class="gray">公司名称:</span>
          <el-input id="companyName"
                    placeholder="请输入内容"></el-input>
        </div>
      </div>
      <!-- 第二列 油品类别 地区 -->
      <div class="flo secondColumn">
        <div class="oil">
          <span class="gray">油品类型:</span>
          <el-select class="oilClass"
                     id="oilClass"
                     @change="oilClassChange(oilclassValue)"
                     v-model="oilclassValue"
                     clearable
                     placeholder="请选择">
            <el-option v-for="(itemclss,index) in oilclassData"
                       :key="index"
                       :label="itemclss.zhName"
                       :value="itemclss.code">
            </el-option>
          </el-select>
        </div>
        <div class="citys">
          <span class="gray">所在地区:</span>
          <el-cascader size="large"
                       id="localCity"
                       clearable
                       :options="cityData"
                       v-model="selectedOptions">
          </el-cascader>
        </div>
      </div>
      <!-- 第三列 规格 交货方式 -->
      <div class="flo thirdColumn">
        <div class="space">
          <span class="gray specifications">规格: </span>
          <el-select class="spaceSel"
                     id="oilPppms"
                     v-model="goodswayValue"
                     v-bind:disabled=bflag
                     placeholder="请选择">
            <el-option v-for="(item,index) in oilPppmsData"
                       :key="index"
                       :label="item.zhName"
                       :value="item.code">
            </el-option>
          </el-select>
        </div>
        <div class="pickupWay">
          <span class="gray">提货方式:</span>
          <el-select class="pickupWaySel"
                     id="pickupWaySel"
                     v-model="goodgetValue"
                     clearable
                     placeholder="请选择">
            <el-option v-for="(itemway,index) in deliveryWayArr"
                       :key="index"
                       :label="itemway.zhName"
                       :value="itemway.code">
            </el-option>
          </el-select>
        </div>
      </div>

      <!-- 搜索按钮 -->
      <div class="flo fourthColumn">
        <input class="searchBtn"
               type="button"
               @click="serechGoods"
               value="搜索" />
      </div>
    </div>
    <!-- tab -->
    <div class="saleListWrap">
      <div class="saleListContent">
        <!-- header部分 -->
        <el-table class="tableHeader"
                  :data="searchGoodsData"
                  border
                  stripe
                  style="width: 100%">
          <!-- 循环列表 -->
          <el-table-column label="发布时间"
                           width="110">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{scope.row.frontReleasedDate}}</span>
            </template>
          </el-table-column>
          <el-table-column label="公司名称"
                           width="210">
            <template slot-scope="scope">
              <span style="margin-left: 10px"
                    class="pointer"
                    :title="scope.row.epMemberName">{{ scope.row.epMemberName}}</span>
            </template>
          </el-table-column>

          <el-table-column label="油品类型"
                           width="110">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.ypname }}</span>
            </template>
          </el-table-column>
          <el-table-column label="规格"
                           width="120">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.ggname }}</span>
            </template>
          </el-table-column>
          <el-table-column label="数量"
                           width="127">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.number }}{{ scope.row.unit }}</span>
            </template>
          </el-table-column>
          <el-table-column label="所在地区"
                           width="160">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.locationProvince}} / {{ scope.row.locationCity}}</span>
            </template>
          </el-table-column>
          <el-table-column label="提货方式"
                           width="120">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.transportWay }}</span>
            </template>
          </el-table-column>
          <el-table-column label="运输方式"
                           width="120">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{scope.row.deliveryWay}}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作"
                           width="100">
            <template slot-scope="scope">
              <router-link class="gotodetail"
                           :to="{ path: 'purchasedetail', query: { uuid:scope.row.uuid}}">
                <el-button size="mini"  type="danger">
                  详情
                </el-button>
              </router-link>
              <el-button size="mini"
                         type="danger"
                         @click="getPrice(scope.row.unit,scope.row.infoId,scope.row.uuid,scope.row.epMemberId)">报价</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <el-pagination background
                       @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page.sync="currentPage"
                       layout="prev, pager, next"
                       :total="total">
        </el-pagination>
      </div>
    </div>
    <div class="hasnone">暂无数据</div>
    <div class="layer"></div>
    <div class="dialogs">
      <el-button type="text"
                 id="dialogs"
                 showConfirmButton="false"
                 width="535"
                 height="354"></el-button>
    </div>
  </div>
</template>

<script>
import url from "@/api/api";
import Utils from "@/components/common/until";
import {
  provinceAndCityData,
  CodeToText,
  TextToCode
} from "element-china-area-data";
export default {
  name: "purchase",
  data () {
    return {
      history: this.$route.query.oilType,
      cityData: provinceAndCityData,
      selectedOptions: [],
      props: {
        value: "label",
        children: "cities"
      },
      startDate: "",
      endDate: "",
      oilclassValue: "",
      oilclassValueIdx: "",
      goodswayValue: "",
      goodgetValue: "",
      // 种类
      oilclassData: "",
      // 规格
      oilPppmsData: "",
      // 运输方式
      tranportwaysData: [],
      // 提货方式
      deliveryWay: '',
      deliveryWayArr: [
        { zhName: '配送', code: 1 },
        { zhName: '自提', code: 2 }
      ],
      // 搜索之后的结果
      searchGoodsData: [],
      // 搜索的总数
      total: 0,
      // 当前的页码
      currentPage: 1,
      bflag: true
    };
  },
  watch: {
    oilclassValue: function (val) {
      if (val == '') {
        this.bflag = true
      } else {
        this.bflag = false
      }
    }
  },
  methods: {
    refresh () {
      window.location.reload()
    },
    // 油品种类变化
    oilClassChange (oilclassValue) {
      this.goodswayValue = "";
      this.oilclassValueIdx = oilclassValue;
      this.getOilPppmsData(this.oilclassValueIdx);
    },
    // 点击搜索
    serechGoods () {
      // 开始日期
      const releaseBeginDate = document.getElementById("startDate").value;
      // 结束日期
      const releaseEndDate = document.getElementById("endDate").value;
      // 判断对应结束日期大于开始日期
      if (
        Utils.formatString(releaseEndDate) <
        Utils.formatString(releaseBeginDate)
      ) {
        this.errorDia("结束日期必须大于开始日期");
        return;
      }
      // 油品类别
      const oilClassification = this.oilclassValueIdx;
      // 规格
      const oilVarieties = this.goodswayValue; // document.getElementById("oilPppms").value;
      // 公司名称
      const epMemberName = document.getElementById("companyName").value;
      // 所在地区 省
      const locationProvince = document
        .getElementsByClassName("el-cascader__label")[0]
        .innerText.split(" / ")[0];
      // 所在地区 市
      const locationCity = document
        .getElementsByClassName("el-cascader__label")[0]
        .innerText.split(" / ")[1];
      // 交货方式
      const deliveryWay = this.goodgetValue; //  document.getElementById("pickupWaySel").value;
      // 10条/页
      const pageSize = 10;
      const pageNum = this.currentPage;
      // 搜索按钮
      this.getData(
        releaseBeginDate,
        releaseEndDate,
        oilClassification,
        oilVarieties,
        epMemberName,
        locationCity,
        locationProvince,
        deliveryWay,
        pageSize,
        pageNum
      );
    },
    // 原油种类接口
    getOilClassData () {
      this.ajax(url.getOilClassData, { oilType: this.$route.query.oilType }, "get")
        .then(res => {
          if (res.data.datas) {
            this.oilclassData = res.data.datas;
            this.oilclassDataIndex = res.data.datas;
          }
          return res;
        })
        .catch(error => { });
    },
    // 规格
    getOilPppmsData (idx) {
      this.ajax(url.getOilPppmsData + "?oilvarietie=" + idx, {}, "get")
        .then(res => {
          if (res.data.datas) {
            this.oilPppmsData = res.data.datas;
          }
          return res;
        })
        .catch(error => { });
    },
    // 获取搜索的结果
    getData (
      releaseBeginDate,
      releaseEndDate,
      oilClassification,
      oilVarieties,
      epMemberName,
      locationCity,
      locationProvince,
      deliveryWay,
      pageSize,
      pageNum
    ) {
      this.ajax(
        url.purchaseList,
        {
          releaseBeginDate,
          releaseEndDate,
          oilClassification,
          oilVarieties,
          epMemberName,
          locationCity,
          locationProvince,
          deliveryWay,
          pageSize: 10,
          pageNum: pageNum,
          infoType: this.$route.query.infoType,
          status : "1,2"
        },
        "post"
      ).then(res => {
        this.searchGoodsData = res.data.datas;
        this.total = Number(res.data.total);
        if (
          (res.data.datas && res.data.datas.length == 0) ||
          res.data.datas == undefined
        ) {
          document.getElementsByClassName("hasnone")[0].style.display = "block";
          document.getElementsByClassName("pagination")[0].style.display =
            "none";
          return;
        }
        document.getElementsByClassName("hasnone")[0].style.display = "none";
        if (this.total < 10) {
          const pagination = document.getElementsByClassName("pagination")[0];
          // pagination.style.display = "none";
        } else {
          const pagination = document.getElementsByClassName("pagination")[0];
          pagination.style.display = "block";
        }
      });
    },
    // 分页方法
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`);
    },
    // 当前页面idx
    handleCurrentChange (val) {
      //  releaseBeginDate,
      // releaseEndDate,
      // oilClassification,
      // oilVarieties,
      // epMemberName,
      // locationCity,
      // locationProvince,
      // pickupWaySel,
      // pageSize,
      // pageNum
      // 开始日期
      this.currentPage = val;
      const releaseBeginDate = document.getElementById("startDate").value;
      // 结束日期
      const releaseEndDate = document.getElementById("endDate").value;
      // 判断对应结束日期大于开始日期
      if (
        Utils.formatString(releaseEndDate) <
        Utils.formatString(releaseBeginDate)
      ) {
        this.errorDia("结束日期必须大于开始日期");
        return;
      }
      // 油品类别
      const oilClassification = this.oilclassValueIdx;
      // 规格
      const oilVarieties = this.goodswayValue; // document.getElementById("oilPppms").value;
      // 公司名称
      const epMemberName = document.getElementById("companyName").value;
      // 所在地区 省
      const locationCity = document
        .getElementsByClassName("el-cascader__label")[0]
        .innerText.split(" / ")[0];
      // 所在地区 市
      const locationProvince = document
        .getElementsByClassName("el-cascader__label")[0]
        .innerText.split(" / ")[1];
      // 交货方式
      const deliveryWay = document.getElementById("pickupWaySel").value;
      // 10条/页
      const pageSize = 10;
      const pageNum = val;
      this.getData(
        releaseBeginDate,
        releaseEndDate,
        oilClassification,
        oilVarieties,
        epMemberName,
        locationCity,
        locationProvince,
        deliveryWay,
        pageSize,
        pageNum
      );
      // console.log(`当前页: ${val}`);
    },
    // 询价弹窗
    getPrice (unit, INFO_ID, UUID, epMemberId) {
      const id = Number(Utils.getCookie("epMemberId"));
      if (epMemberId === id) {
        this.errorDia("当前用户不能对自己的订单报价");
        return;
      }
      this.ajax(url.selctLogin, {}, 'get').then((res) => {
        if (res.data.status != 0) {
          this.$message('请登录');
        } else if (res.data.status == 0 && !res.data.datas.epMemberId) {
          this.$message('请先绑定企业');
        } else {
          this.$alert(
            `
          <div class="price">
          <span class="gray"><em>*</em>议价(元/${unit}):</span>
          <input id="priceinput"></input>
          </div>
          <div class='contacts'>
          <span><em>*</em>联系人:</span>
          <input id="contactsinput"></input>
          </div>
          <div class="phone">
          <span class="gray"><em>*</em>手机:</span>
          <input id="phoneinput"></input>
          </div>`,
            {
              confirmButtonText: "提交",
              confirmButtonClass: "dialogsBtn",
              // showConfirmButton: false,
              dangerouslyUseHTMLString: true,
              // callback: action => {},
              beforeClose: (action, instance, done) => {
                if (action === "confirm") {
                  // (number, price, contacts, tel, type, needId, needUuid
                  const price = document.getElementById("priceinput").value;
                  const contacts = document.getElementById("contactsinput").value;
                  const tel = document.getElementById("phoneinput").value;
                  if (price == "") {
                    this.errorDia("请填写议价");
                    return;
                  } else {
                    if (!Utils.decimal2(price)) {
                      this.errorDia("议价不能超过两位小数");
                      return;
                    }
                  }

                  if (contacts == "") {
                    this.errorDia("请填写联系人");
                    return;
                  } else {
                    if (!Utils.isName(contacts)) {
                      this.errorDia("联系人请填写中文或英文");
                      return;
                    }
                  }

                  if (tel == "") {
                    this.errorDia("请填写手机号");
                    return;
                  } else {
                    if (!Utils.isvalidPhone(tel)) {
                      this.errorDia("请填写正确的手机号码");
                      return;
                    }
                  }

                  this.postPriceData(
                    price,
                    contacts,
                    tel,
                    2,
                    INFO_ID,
                    UUID,
                    res => {
                      if (res == 0) {
                        this.successDia("报价成功");
                        done();
                        document.getElementById("priceinput").value = "";
                        document.getElementById("contactsinput").value = "";
                        document.getElementById("phoneinput").value = "";
                      } else {
                        this.errorDia("报价失败，请稍后再试");
                      }
                    }
                  );
                } else {
                  document.getElementById("priceinput").value = "";
                  document.getElementById("contactsinput").value = "";
                  document.getElementById("phoneinput").value = "";
                  done();
                }
              }
            }
          );
        }
      })
    },

    // number	必填	求购数量
    // price	必填	议价
    // contacts	必填	联系人
    // tel	必填	手机
    // type	必填	类型(1: 销售是询价2: 采购是报价)
    // needId	必填	需求Id（销售信息的infoId）
    // needUuid	必填	需求uuid（销售信息的uuid）
    // 报价接口
    postPriceData (price, contacts, tel, type, needId, needUuid, callback) {
      this.ajax(
        url.insert,
        {
          price,
          contacts,
          tel,
          type,
          needId,
          needUuid
        },
        "post"
      ).then(res => {
        callback(res.data.status);
      });
    },
    // error msg
    errorDia (msg) {
      this.$message({
        showClose: true,
        message: msg,
        type: "error"
      });
    },
    successDia (msg) {
      this.$message({
        message: msg,
        type: "success"
      });
    },

    //获取运输方式下拉列表
    getTranportwaysData () {
      this.ajax(url.getTranportwaysData, {}, "get")
        .then(res => {
          if (res.data.datas) {
            this.tranportwaysData = res.data.datas;
          }
          return res;
        })
        .catch(error => { });
    }
  },
  components: {},
  watch: {},
  created () { },

  mounted () {
    // 油品种类
    this.getOilClassData();
    // 规格
    this.getOilPppmsData(1);
    // 获取运输方式下拉列表
    this.getTranportwaysData();
    // 获取搜索的数据
    // releaseBeginDate,
    //   releaseEndDate,
    //   oilClassification,
    //   oilVarieties,
    //   epMemberName,
    //   locationCity,
    //   locationProvince,
    //   pickupWaySel,
    //   pageSize,
    //   pageNum
    this.getData("", "", "", "", "", "", "", "", 10, 1);
  }
};
</script>

<style >
.none {
  display: none !important;
}
.flo {
  float: left;
}
.gray {
  color: #666;
}
.saleWrap {
  width: 1180px;
  height: auto;
  overflow: hidden;
  margin: 0 auto;
}
.smallTitle {
  font-size: 14px;
  color: #333;
  height: 26px;
  line-height: 26px;
}

/* 搜索条件 */
.firstColumn {
  width: 333px;
}
.searchDate {
  width: 100%;
  height: 36px;
  margin-bottom: 9px;
}
.searchfactirs {
  padding: 25px 20px;
  width: 1138px;
  height: 82px;
  border: 1px solid #ebebeb;
  background: #f5f5f5;
}
/* 覆盖掉原有的elementui样式 */
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 126px;
  height: 36px;
  line-height: 36px;
}
.el-input__inner {
  height: 36px;
}
.demonstration {
  font-size: 14px;
  color: #666;
}
.secondColumn {
  margin-left: 31px;
}
.firstfloat {
  float: left;
}

.line {
  margin: 0 3.5px;
  height: 36px;
  display: flex;
  align-items: center;
  width: 8px;
}
.el-input {
  width: 267px;
}
.oilClass .el-input {
  width: 247px;
}
.citySheng .el-input,
.cityShi .el-input {
  width: 118px;
}

.cityShi {
  margin-left: 6px;
}
.citySheng .el-input__inner,
.cityShi .el-input__inner {
  width: 118px;
}
.oil {
  margin-bottom: 9px;
}

.fourthColumn {
  margin-left: 36px;
}
.thirdColumn {
  margin-left: 31px;
}

.searchBtn {
  width: 80px;
  height: 36px;
  line-height: 36px;
  background: #f36523;
  color: #fff;
  border-radius: 3px;
}
.space .specifications {
  width: 60px;
  display: inline-block;
  height: 36px;
  text-align: left;
}

/* table */
.el-table-column th {
  text-align: center;
}
.el-table__empty-block {
  display: none;
}
.saleListWrap {
  margin-top: 20px;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.pagination .el-pagination {
  display: flex !important;
  justify-content: center;
}

.el-pagination.is-background .el-pager li:not(.disabled).active {
  background: #f36523 !important;
}
.el-pagination.is-background .btn-next,
.el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  color: #666;
}

.tableHeader th {
  height: 40px;
  background: #ebebeb;
}
.cell {
  text-align: center;
}
.el-table--border td,
.el-table--border th,
.el-table__body-wrapper .el-table--border.is-scrolling-left ~ .el-table__fixed {
  border-right: 1px solid #e6e6e6;
}
/* .cycleList th {
  height: 102px;
  line-height: 102px;
} */
.el-button + .el-button {
  margin-top: 8px;
  margin-left: 0;
}

.el-message-box {
  width: 535px !important;
  overflow: hidden !important;
  padding: 10px 0 35px !important;
}

.dialogs {
  color: #666;
}
.numbers {
  margin-top: 30px;
}
.numbers,
.price,
.contacts,
.phone {
  margin-bottom: 10px;
}

.numbers span,
.price span,
.contacts span,
.phone span {
  display: inline-block;
  width: 170px;
  height: 36px;
  line-height: 36px;
  text-align: center;
}

.numbers input,
.price input,
.contacts input,
.phone input {
  border-radius: 3px;
  width: 257px;
  height: 26px;
  padding: 5px;
  line-height: 36px;
  border: 1px solid #ccc;
}

.el-message-box__btns {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-top: 40px;
}

.dialogsBtn {
  width: 125px;
  height: 36px;
  background: #f36523;
  border-radius: 2px;
  color: #fff;
  border: none;
}
.dialogsBtn:hover {
  background: #f36523;
}
.el-message-box__btns {
  padding: 0;
  margin: 0;
}

.el-button--primary:focus,
.el-button--primary:hover {
  background: #f36523;
}
.el-button--primary {
  color: #fff;
  background-color: #f36523 !important;
  border-color: #f36523 !important;
}

.el-input--suffix {
  width: 247px;
}
.el-input--suffix .el-input__inner {
  padding-right: 0px;
}
.hasnone {
  width: 100%;
  height: 100px;
  display: none;
  text-align: center;
  line-height: 100px;
}
.gotodetail {
  width: 56px;
  height: 28px;
  display: inline-block;
  margin-bottom: 8px;
}
#companyName {
  width: 267px !important;
}
.companyName .el-input {
  width: 267px !important;
}
.el-message-box__headerbtn {
  width: 30px;
  height: 30px;
  z-index: 2;
}
.el-button--danger {
  color: #fff;
  background-color: #f36523 !important;
  border-color: #f36523 !important;
}
.el-message-box__message em {
  color: red;
  position: relative;
  left: -6px;
  top: 3px;
}
</style>



