import Axios from 'axios'

Axios.defaults.timeout = 10000;
// 判断是否是ajax异步请求
Axios.defaults.headers['X-Requested-with'] = 'XMLHttpRequest';
// form表单提交格式
Axios.defaults.headers.post['Content-Type'] = 'application/json; charset=UTF-8'; // 'application/x-www-form-urlencoded';
// Axios.defaults.withCredentials = true

/**
 * 这是一个请求方法 test
 * @param {string} method 请求方法
 * @param {string} url  请求链接
 * @param {object} params 请求参数
 * @return {Promise} 返回promise
 */
export function http(url, obj, method) {
  let modeKey = ['post', 'put'].includes(method.toLowerCase()) ? 'data' : 'params';
  return new Promise((resolve, reject) => {
    Axios({
        method: method.toLowerCase(),
        url,
        [modeKey]: obj || {},
        withCredentials: true
      })
      .then(response => {
        resolve(response, url);
      })
      .catch(error => {
        // if (error.response && error.response.status === 500) {
        //   console.log(error.message, "接口异常，捕获到了,后期可以跳转到错误页,统一处理");
        // }
        console.log(error);
        reject(error);
      });
  });
}

export default {
  http: http,
}

