<template>
  <div class="clearfix w1180">
    <div class="fl realtime">
      <p>时实报价</p>
      <ul class="clearfix">
        <li v-for="(item,index) in datas1"
            @click="toggle1(index)"
            :class="{'active':index ==checkindex1 }" class="pointer">
          {{item}}
        </li>
      </ul>
      <div id="realTimeThree"
           v-show="0==this.checkindex1"></div>
      <div id="realTimeOne"
           v-show="1==this.checkindex1"></div>
      <div id="realTimeTwo"
           v-show="2==this.checkindex1"></div>

    </div>
    <div class="fr price">
      <p>价格预测</p>
      <ul class="clearfix">
        <li v-for="(item,index) in datas2"
            @click="toggle2(index)"
            :class="{'active':index ==checkindex2 }" class="pointer">
          {{item}}
        </li>
      </ul>
      <div id="priceOne"
           v-show="0==this.checkindex2"></div>
      <div id="priceTwo"
           v-show="1==this.checkindex2"></div>
    </div>

  </div>
</template>

<script>
import echarts from 'echarts'
import url from '@/api/api'
export default {
  data () {
    return {
      datas1: ['ICE Brent', 'WTI', 'DME'],
      datas2: ['ICE Brent', 'WTI'],
      checkindex1: 0,
      checkindex2: 0,
      eachData: {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: []
        },
        grid: {
          left: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          name: '111',
          type: 'line',
          stack: '总量1',
          data: []
        }, {
          name: '222',
          type: 'line',
          stack: '总量2',
          data: []
        },
        {
          name: '333',
          type: 'line',
          stack: '总量3',
          data: []
        }]
      },
      options: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          backgroundColor: 'rgba(245, 245, 245, 0.8)',
          borderWidth: 1,
          borderColor: '#ccc',
          padding: 10,
          textStyle: {
            color: '#000'
          }
        },
        axisPointer: {
          link: { xAxisIndex: 'all' },
          label: {
            backgroundColor: '#777'
          }
        },
        grid: [
          {
            top: '10%',
            left: '12%',
            right: '9%',
            height: '50%'
          },
          {
            left: '12%',
            right: '9%',
            top: '75%',
            height: '12%'
          }
        ],
        xAxis: [
          {
            type: 'category',
            data: [],
            scale: true,
            boundaryGap: false,
            axisLine: { onZero: false },
            splitLine: { show: false },
            splitNumber: 20,
            min: 'dataMin',
            max: 'dataMax',
            axisPointer: {
              z: 100
            }
          },
          {
            type: 'category',
            gridIndex: 1,
            data: [],
            scale: true,
            boundaryGap: false,
            axisLine: { onZero: false },
            axisTick: { show: false },
            splitLine: { show: false },
            axisLabel: { show: false },
            splitNumber: 20,
            min: 'dataMin',
            max: 'dataMax'
          }
        ],
        yAxis: [
          {
            scale: true,
            splitArea: {
              show: true
            }
          },
          {
            scale: true,
            gridIndex: 1,
            splitNumber: 2,
            axisLabel: { show: false },
            axisLine: { show: false },
            axisTick: { show: false },
            splitLine: { show: false }
          }
        ],

        series: [
          {

            type: 'candlestick',
            data: []
          },
          {
            name: 'Volume',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            data: []
          }
        ]
      }
    }
  },
  methods: {
    toggle1: function (index) {
      this.checkindex1 = index
    },
    toggle2: function (index) {
      this.checkindex2 = index
    },
    tabEachOne: function () { // 实时报价
      var This = this;
      var myChart1 = echarts.init(document.getElementById('realTimeOne'));
      var myChart2 = echarts.init(document.getElementById('realTimeTwo'));
      var myChart3 = echarts.init(document.getElementById('realTimeThree'));
      // 指定图表的配置项和数据
      function eachZong (obj, obj2, min, max) {
        var option = This.eachData
        option.series = [{
          name: '',
          type: 'line',
          stack: '总量',
          data: []
        }]
        option.grid.top = '15%';
        option.legend.data = [''];
        option.xAxis.data = [];
        option.series[0].data = [];

        for (var i = 0, j = obj.length - 1; i < obj.length; i++ , j--) {

          var changeTime = obj[j].indexDate;
          var arrTime = changeTime.split('-');
          changeTime = arrTime[1] + '/' + arrTime[2];

          option.xAxis.data.push(changeTime);    //  x 轴
          option.series[0].data.push(obj[j].smeiValue);    // y 轴
        }
        option.yAxis.min = min;
        option.yAxis.max = max;
        obj2.setOption(option);
      }
      // 根据数据  生成 图表
      this.ajax(url.echarts1, {}, 'get').then((res) => {
        eachZong(res.data.datas[0].priceList, myChart1, res.data.datas[0].min, res.data.datas[0].max);
        eachZong(res.data.datas[1].priceList, myChart2, res.data.datas[1].min, res.data.datas[1].max);
        eachZong(res.data.datas[2].priceList, myChart3, res.data.datas[2].min, res.data.datas[2].max);
      })
    },
    priceBrent: function () {//brent价格预测
      // 根据数据  生成 图表
      var text = '';
      var subtext1 = '';
      var subtext2 = '';
      var myChartone = echarts.init(document.getElementById('priceOne'));
      // var option = this.eachData
      var option = $.extend(true, option, this.eachData);
      // 根据数据  生成 图表
      this.ajax(url.echarts2, {}, 'get').then((res) => {
        option.series[2].itemStyle = {
          normal: {
            lineStyle: {
              width: 2,
              type: 'dotted'  //'dotted'虚线 'solid'实线
            }
          }
        };
        option.tooltip = {
          trigger: 'axis',
          formatter: function (params) {
            var text = params[0].name;
            var value = params[0].data;
            if (value) {
              text += '<br />预测值：' + value;
            }
            if (params.length > 1) {
              var predict = params[1].data;
              if (predict) {
                text += '<br />实际值：' + predict;
              }
            }
            return text;
          }
        };
   
        for (var i = 0; i < res.data.data.length; i++) {
          
          var obj = res.data.data[i];
          option.xAxis.data.push(obj.date);
          if (i < res.data.data.length - 1) {
            option.series[0].data.push(res.data.data[i].predict);   //predict
            option.series[1].data.push(res.data.data[i].value);     //value
          }

          if (i < res.data.data.length - 2) {
            option.series[2].data.push(null);
          } else {
            option.series[2].data.push(res.data.data[i].predict);
          }
        }
        var minArray = [];
        minArray.push(Math.min.apply(null, option.series[0].data));
        minArray.push(Math.min.apply(null, option.series[1].data));
        var mins2 = (Math.min.apply(null, minArray) - 5).toFixed(0)
        var lastObj1 = res.data.data[res.data.data.length - 1];
        var lastObj2 = res.data.data[res.data.data.length - 2];
        text = lastObj1.predict;
        subtext1 = (lastObj1.predict - lastObj2.value).toFixed(2);
        subtext2 = (subtext1 / lastObj2.value * 100).toFixed(2);
        option.legend.data.push('预测值', '实际值');
        option.series[0].name = '预测值'
        option.series[2].name = '预测值'
        option.series[1].name = '实际值'

        option.title = {
          text: text,
          subtext: subtext1 + '  ' + subtext2 + '%',
          left: 'right',
          subtextStyle: {
            color: '#FF3034'
          }
        };
        option.yAxis = {
          min: mins2
        }
      
        myChartone.setOption(option);
      })
    },
    priceWti: function () {//wti价格预测
      var text = '';
      var subtext1 = '';
      var subtext2 = '';
      var myChartTwo = echarts.init(document.getElementById('priceTwo'));
      // var option = this.eachData
      var option = $.extend(true, option, this.eachData);
      // 根据数据  生成 图表
      this.ajax(url.echarts3, {}, 'get').then((res) => {
        option.series[2].itemStyle = {
          normal: {
            lineStyle: {
              width: 2,
              type: 'dotted'  //'dotted'虚线 'solid'实线
            }
          }
        };
       
        option.tooltip = {
          trigger: 'axis',
          formatter: function (params) {
            var text = params[0].name;
            var value = params[0].data;
            if (value) {
              text += '<br />预测值：' + value;
            }
            if (params.length > 1) {
              var predict = params[1].data;
              if (predict) {
                text += '<br />实际值：' + predict;
              }
            }
            return text;
          }
        };


        for (var i = 0; i < res.data.data.length; i++) {
          var obj = res.data.data[i];
          option.xAxis.data.push(obj.date);
          if (i < res.data.data.length - 1) {
            option.series[0].data.push(res.data.data[i].predict);   //predict
            option.series[1].data.push(res.data.data[i].value);     //value
          }

          if (i < res.data.data.length - 2) {
            option.series[2].data.push(null);
          } else {
            option.series[2].data.push(res.data.data[i].predict);
          }
        }
        var minArray = [];
        minArray.push(Math.min.apply(null, option.series[0].data));
        minArray.push(Math.min.apply(null, option.series[1].data));
        var mins1 = (Math.min.apply(null, minArray) - 5).toFixed(0)

        var lastObj1 = res.data.data[res.data.data.length - 1];
        var lastObj2 = res.data.data[res.data.data.length - 2];
        text = lastObj1.predict;
        subtext1 = (lastObj1.predict - lastObj2.value).toFixed(2);
        subtext2 = (subtext1 / lastObj2.value * 100).toFixed(2);
        option.legend.data.push('预测值', '实际值');

        option.series[0].name = '预测值'
        option.series[1].name = '实际值'
        option.series[2].name = '预测值'
        option.title = {
          text: text,
          subtext: subtext1 + '  ' + subtext2 + '%',
          left: 'right',
          subtextStyle: {
            color: '#FF3034'
          }
        };

        option.yAxis = {
          min: mins1
        }
        myChartTwo.setOption(option);
      })
    },
  },
  mounted () {
    this.priceBrent()
    this.tabEachOne();
    this.priceWti()
  },
  created () {

  }
}
</script>

<style scoped>
.realtime,
.price {
  width: 450px;
  height: 320px;
  margin: 20px 0;
  padding:0 40px;
  border: 1px solid #EDEDED;
}
.price p,
.realtime p {
  font-size: 18px;
  height: 48px;
  line-height: 48px;
  text-align: center;
  border-bottom: 1px solid #EDEDED;
}

#realTimeOne,
#realTimeTwo,
#realTimeThree,
#priceOne,
#priceTwo {
  width: 380px;
  height: 185px;
}
ul li {
  width: 70px;
  float: left;
  padding: 10px;
  text-align: center;
  font-size: 14px;
  border-bottom: 2px solid #ffffff;
}
.active {
  color: #f36523;
  border-bottom: 2px solid #f36523;
}
</style>


