<template>
  <div class="hall">
    <carousel/>
    <echarts/>
    <ul class="clearfix">
      <li v-for="(item,index) in oliType"
          @click="toggle(index)"
          :class="{'active':index ==checkindex }"
          class="pointer">
        {{item}}
      </li>
    </ul>
    <div v-show="!checkindex">
      <sale ref="refresh3" />
      <purchase ref="refresh4" />
    </div>
    <div v-show="checkindex">
      <saleRaw ref="refresh1" />
      <purchaseRaw ref="refresh2" />
    </div>

  </div>
</template>

<script>
import carousel from './carousel.vue'
import echarts from './echarts.vue'
import sale from './sale.vue'
import purchase from './purchase.vue'
import saleRaw from './saleRaw.vue'
import purchaseRaw from './purchaseRaw.vue'
export default {
  name: 'hall',
  data () {
    return {
      oliType: ['船油', '原料油'],
      checkindex: 0
    }
  },
  components: {
    carousel,
    echarts,
    sale,
    purchase,
    saleRaw,
    purchaseRaw,
  },
  watch: {
  },
  created () {
    if (this.$route.query.oilType == 1) {
      this.checkindex = 0
      // this.scrollTop(300)
    } else if (this.$route.query.oilType == 9) {
      this.checkindex = 1
      // this.scrollTop(500)
    }

  },
  methods: {
    toggle: function (index) {
      this.checkindex = index
      if (index == 0) {
        this.$refs.refresh3.getData()
        this.$refs.refresh4.getData()
      } else {

        this.$refs.refresh1.getData()
        this.$refs.refresh2.getData()
      }
    },
    scrollTop: function (num) {
      window.scrollTo(0,num);
    }
  },
  mounted () {
  }
}
</script >

<style scoped>
ul {
  text-align: center;
  padding: 30px 0;
}
ul li {
  display: inline-block;
  width: 100px;
  height: 36px;
  line-height: 36px;
  border: 1px solid #f36523;
}
.active {
  color: #fff;
  background: #f36523;
}
</style>



