<template>
  <div class="sale clearfix w1180">
    <div class="fl nav">
      <router-link :to="{path:'/salelist',query: {infoType: '1,2,3',oilType:'1'}}">
        <ul>
          <li class="
                   active
                   ">销售</li>
          <li>优质客户认证</li>
          <li>精准匹配，更多商机</li>
        </ul>
      </router-link>
    </div>
    <div class="fr
                   container
                   ">
      <p class="new
                   ">
        最新推荐</p>
      <div class="team"
           v-for="(item,index) in  datas "
           :key="index">
        <p class="more">
          <a :href="'#/salelist?infoType='+(index+1)+'&oilType=1'"
             class="fr">查看更多</a>{{format(item.info_type)}}</p>
        <ul class="clearfix
                   ">
          <li v-for="(val,index)
                   in
                   item.datas
                   "
              :key="index
                   "
              v-if='index<3'>
            <router-link :to="{
                   path:
                   'goodsdetail',
                   query:
                   {
                   uuid:val.UUID}}
                   ">
              <img :src='val.epLogo'
                   alt="
                   "
                   v-show="val.epLogo
                   ">
              <img src='/static/oilbank.png'
                   alt="
                   "
                   v-show="!val.epLogo
                   ">
              <div class="con
                   ">
                <p class="company
                   nowrap
                   ">{{val.EP_MEMBER_NAME}}</p>
                <p class="place
                   nowrap
                   ">{{val.LOCATION_PROVINCE}} / {{val.LOCATION_CITY}}</p>
                <p class="oil
                   nowrap
                   ">{{val.OIL_CLASSIFICATION}} {{val.OIL_VARIETIES}}</p>
                <p class="unit
                   nowrap
                   ">{{val.UNIT_PRICE}}元/{{val.UNIT}}</p>
              </div>
              <em class="type
                   ">{{val.DELIVERY_WAY}}</em>
              <em class="type1
                   "
                  :class="{
                   'bg':
                   val.TRANSPORT_WAY=='配送'
                   }
                   "></em>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'sale',
  data () {
    return {
      datas: []
    }
  },
  components: {
  },
  watch: {
  },
  created () {
  },
  methods: {
    format: function (type) {
      if (type == 1) {
        return '船燃贸易'
      } else if (type == 2) {
        return '船舶加油(内贸)'
      } else if (type == 3) {
        return '船舶加油(保税)'
      }
    },
    getData: function () {
      this.ajax(url.home, {status : "1,2"}, 'get').then((res) => {
        if (!!res.data.datas && res.data.datas.length !== 0) {
          this.datas = res.data.datas
        }
      })
    }
  },
  mounted () {
    this.getData()
  }
}
</script >

<style scoped>
.nav {
  width: 240px;
  height: 867px;
  background: url('/static/list.png') no-repeat center center;
  opacity: 0.8;
  margin-bottom: 30px;
}
.nav ul {
  padding: 700px 0 0 20px;
}
.nav ul li {
  line-height: 42px;
  height: 42px;
  font-size: 16px;
  color: #ffffff;
}
.active {
  font-size: 24px !important;
}
.team {
  padding-left: 30px;
  height: 259px;
}
.new {
  font-size: 18px;
  color: rgba(51, 51, 51, 1);
  padding: 18px 20px 15px 30px;
  margin-bottom: 5px;
  border-bottom: 1px solid #e8e8e8;
  font-weight: bold;
}
.more {
  padding: 15px 20px 15px 0;
  font-size: 16px;
  color: rgba(51, 51, 51, 1);
  font-weight: bold;
}
.more a {
  font-size: 14px;
  color: rgba(51, 51, 51, 1);
  line-height: 24px;
  position: relative;
  padding-right: 16px;
  font-weight: normal;
}
.more a:after {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-bottom: 1px solid #000;
  border-left: 1px solid #000;
  transform: rotate(-135deg);
  position: absolute;
  top: 9px;
  right: 4px;
}

.container {
  width: 939px;
  margin-bottom: 30px;
  border: 1px solid #e8e8e8;
  border-left: none;
}
.container li {
  width: 280px;
  height: 200px;
  position: relative;
  float: left;
  margin-right: 20px;
  margin-bottom: 20px;
  border: 1px solid #e8e8e8;
}
.container li img {
  width: 56px;
  height: 56px;
  position: absolute;
  left: 20px;
  top: 20px;
}
.con {
  padding-top: 24px;
  color: rgba(51, 51, 51, 1);
}
.type {
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: rgba(153, 153, 153, 1);
  line-height: 18px;
}
.bg {
  background: url('/static/bg2.png') no-repeat center center !important;
}
.type1 {
  position: absolute;
  bottom: 10px;
  left: 0px;
  width: 53px;
  height: 24px;
  background: url('/static/bg1.png') no-repeat center center;
  border-radius: 0px 30px 30px 0px;
  font-size: 12px;
  color: #fff;
  line-height: 24px;
  text-align: center;
}
.unit {
  font-size: 18px;
  color: rgba(243, 101, 35, 1);
  line-height: 24px;
  padding-left: 85px;
}
.oil {
  font-size: 18px;
  line-height: 24px;
  padding-bottom: 5px;
  padding-left: 85px;
}
.place {
  line-height: 18px;
  padding-bottom: 20px;
  margin: 0 22px 15px;
  border-bottom: 1px solid #e8e8e8;
  padding-left: 63px;
}
.company {
  font-size: 16px;
  line-height: 20px;
  padding-bottom: 10px;
  padding-left: 85px;
}
</style>



