<template>
  <div class="goodsDetailWrap">
    <p class="smallTitle">
      <span class="pointer"
            @click="go()">返回</span> >
      <span class="pointer"
            @click="refresh">销售详情</span>
    </p>
    <div class="goodsDetailContent">
      <div class="goodsDetailLeft">
        <div class="goodsBigImg">
          <ul class="goodsBigImg"
              v-if="result.chmentsVOList && result.chmentsVOList.length >0">
            <li class="bigimgs"
                v-if="result.chmentsVOList = result.chmentsVOList.length >4 ? result.chmentsVOList.slice(0,4) :  result.chmentsVOList"
                v-for="(item,index) in result.chmentsVOList"
                :key="item.index"
                v-bind:class="{ active: (index == idx) }">
              <img :src="item.path"
                   width="480"
                   height="320" />
            </li>
          </ul>
          <ul class="goodsBigImg"
              v-else>
            <li class="bigimgs active">
              <img :src="imgUrl"
                   width="480"
                   height="320" />
            </li>
          </ul>
        </div>
        <div class="goodsSmallImgCon">
          <ul class="goodsSmallImg">
            <li v-if="result.chmentsVOList = result.chmentsVOList.length >4 ? result.chmentsVOList.slice(0,4) :  result.chmentsVOList"
                v-for="(item,val) in result.chmentsVOList"
                :key="val"
                @click="replace(val)">
              <img :src="item.path"
                   width="90"
                   height="60" /></li>
          </ul>
        </div>
      </div>
      <div class="goodsDetailRight">
        <p class="goodsMsg">{{this.result.epMemberName}} {{result.oilClassification}}{{result.oilVarieties}} {{result.locationProvince}} {{result.locationCity}}</p>
        <p class="goodsPrice gray">单价 :
          <span>{{result.unitPrice}}
            <i>元/{{result.unit}}</i>
          </span>
        </p>
        <p class="goodsCity gray">所在地区 :
          <span>{{result.locationProvince}} {{result.locationCity}}</span>
        </p>
        <p class="goodsDate gray">发布日期 :
          <span>{{result.releasedDate}}</span>
        </p>
        <input type="button"
               class="conversation"
               @click="getPrice(result.unit,result.infoId,result.uuid,result.epMemberId)"
               value="询价" />
      </div>
    </div>
    <div class="goodsMessage">
      <p class="title">商品信息</p>
      <ul class="goodsIntro">
        <li>
          <span>油品类型 :</span>
          <p>{{result.oilClassification}}</p>
        </li>
        <li>
          <span>规格 :</span>
          <p>{{result.oilVarieties}}</p>
        </li>
        <li>
          <span>提货方式 :</span>
          <p>{{result.transportWay}}</p>
        </li>
        <li>
          <span>运输方式 :</span>
          <p>{{result.deliveryWay}}</p>
        </li>
        <li>
          <span>单价 :</span>
           <p>{{result.unitPrice}}元/{{result.unit}}</p>
        </li>
        <li>
          <span>详细说明 :</span>
          <p>
            {{result.detailedDescription}}
          </p>
        </li>
      </ul>
    </div>
    <div class="companyMessage">
      <p class="title">公司信息</p>
      <ul class="goodsIntro">
        <li>
          <span>公司名称 :</span>
          <p>{{enterprise.epFullName}}</p>
        </li>
        <li>
          <span>注册地址 :</span>
          <p>{{enterprise.epType}}</p>
        </li>
        <li>
          <span>企业资质 :</span>
          <p>{{enterprise.credentials}}</p>
        </li>
        <li>
          <span>经营范围 :</span>
          <p>{{enterprise.epScope}}</p>
        </li>
        <li>
          <span>公司简介 :</span>
          <p>{{enterprise.description}}</p>
        </li>
      </ul>
    </div>
    <div class="dialogs">
      <el-button type="text"
                 id="dialogs"
                 showConfirmButton="false"
                 width="535"
                 height="354"></el-button>
    </div>
  </div>
</template>

<script>
import url from "@/api/api";
import Utils from "@/components/common/until";
// import imgUrl from "./imges/1.png";
export default {
  name: "goodsdetail",
  data () {
    return {
      value: "",
      result: "",
      idx: "0",
      // 公司信息
      enterprise: {}
      // imgUrl: "@/imges/1.png"
    };
  },
  components: {},
  watch: {},
  created () {
    this.imgUrl = require("@/" + "components/GoodsDetail/imges/1.png");
  },
  methods: {
    refresh () {
      window.location.reload()
    },
    go () {
      this.$router.go(-1)
    },
    // 替换大图
    replace (key) {
      this.idx = key;
    },
    getData () {
      this.ajax(
        url.detailsInfo,
        {
          uuid: this.$route.query.uuid
        },
        "post"
      ).then(res => {
        this.result = res.data.datas.infoVO;
        this.enterprise = res.data.datas.enterprise;
        console.log(res);
      });
    },
    // 询价弹窗
    getPrice (unit, INFO_ID, UUID, epMemberId) {
      const id = Number(Utils.getCookie("epMemberId"));
      if (epMemberId === id) {
        this.errorDia("当前用户不能对自己的订单询价");
        return;
      }
      this.ajax(url.selctLogin, {}, 'get').then((res) => {
        if (res.data.status != 0) {
          this.$message('请登录');
          return;
        } else if (res.data.status == 0 && !res.data.datas.epMemberId) {
          this.$message('请先绑定企业');
          return;
        } else {
          this.$alert(
            `<div class="numbers">
          <span class="gray"><em>*</em>求购数量(${unit}):</span>
          <input id="numbersinput"></input>
          </div>
          <div class="price">
          <span class="gray"><em>*</em>议价(元/${unit}):</span>
          <input id="priceinput"></input>
          </div>
          <div class='contacts'>
          <span><em>*</em>联系人:</span>
          <input id="contactsinput"></input>
          </div>
          <div class="phone">
          <span class="gray"><em>*</em>手机:</span>
          <input id="phoneinput"></input>
          </div>`,
            {
              confirmButtonText: "提交",
              confirmButtonClass: "dialogsBtn",
              // showConfirmButton: false,
              dangerouslyUseHTMLString: true,
              // callback: action => {},
              beforeClose: (action, instance, done) => {
                if (action === "confirm") {
                  // (number, price, contacts, tel, type, needId, needUuid
                  const number = document.getElementById("numbersinput").value;
                  const price = document.getElementById("priceinput").value;
                  const contacts = document.getElementById("contactsinput").value;
                  const tel = document.getElementById("phoneinput").value;
                  if (number == "") {
                    this.errorDia("请填写求购数量");
                    return;
                  } else {
                    if (!Utils.decimal3(number)) {
                      this.errorDia("求购数量不能超过三位小数");
                      return;
                    }
                  }

                  if (price == "") {
                    this.errorDia("请填写议价");
                    return;
                  } else {
                    if (!Utils.decimal2(price)) {
                      this.errorDia("议价不能超过两位小数");
                      return;
                    }
                  }

                  if (contacts == "") {
                    this.errorDia("请填写联系人");
                    return;
                  } else {
                    if (!Utils.isName(contacts)) {
                      this.errorDia("联系人请填写中文或英文");
                      return;
                    }
                  }

                  if (tel == "") {
                    this.errorDia("请填写手机号");
                    return;
                  } else {
                    if (!Utils.isvalidPhone(tel)) {
                      this.errorDia("请填写正确的手机号码");
                      return;
                    }
                  }
                  this.postPriceData(
                    number,
                    price,
                    contacts,
                    tel,
                    1,
                    INFO_ID,
                    UUID,
                    res => {
                      if (res == 0) {
                        this.successDia("询价成功");
                        done();
                        document.getElementById("numbersinput").value = "";
                        document.getElementById("priceinput").value = "";
                        document.getElementById("contactsinput").value = "";
                        document.getElementById("phoneinput").value = "";
                      } else {
                        this.errorDia("询价失败，请稍后再试");
                      }
                    }
                  );
                } else {
                  document.getElementById("numbersinput").value = "";
                  document.getElementById("priceinput").value = "";
                  document.getElementById("contactsinput").value = "";
                  document.getElementById("phoneinput").value = "";
                  done();
                }
              }
            }
          );
        }
      })
    },
    // number	必填	求购数量
    // price	必填	议价
    // contacts	必填	联系人
    // tel	必填	手机
    // type	必填	类型(1: 询价2: 报价)
    // needId	必填	需求Id（销售信息的infoId）
    // needUuid	必填	需求uuid（销售信息的uuid）
    // 报价接口
    postPriceData (
      number,
      price,
      contacts,
      tel,
      type,
      needId,
      needUuid,
      callback
    ) {
      this.ajax(
        url.insert,
        {
          number,
          price,
          contacts,
          tel,
          type,
          needId,
          needUuid
        },
        "post"
      ).then(res => {
        callback(res.data.status);
      });
    },
    // error msg
    errorDia (msg) {
      this.$message({
        showClose: true,
        message: msg,
        type: "error"
      });
    },
    successDia (msg) {
      this.$message({
        message: msg,
        type: "success"
      });
    }
  },
  mounted () {
    this.getData();
  }
};
</script>

<style >
li.bigimgs {
  display: none;
}

li.active {
  display: block;
}

.gray {
  color: #666;
}

.flo {
  float: left;
}
.goodsDetailWrap {
  width: 1180px;
  height: auto;
  margin: 0 auto;
  padding-top: 30px;
}

.goodsDetailContent {
  width: 100%;
  height: 430px;
  overflow: hidden;
  border-bottom: 1px solid rgba(236, 236, 236, 1);
}
.goodsDetailLeft {
  float: left;
}
.goodsDetailRight {
  float: left;
  margin-left: 50px;
  width: 400px;
  height: 320px;
  /* background: wheat; */
}

.goodsDetailRight p {
  margin-bottom: 10px;
}
.goodsBigImg {
  width: 480px;
  height: 320px;
}

.goodsSmallImg {
  width: 88%;
  margin: 0 auto;
}

.goodsSmallImg li {
  width: 90px;
  height: 60px;
  float: left;
  margin: 0 7px;
  cursor: pointer;
}

.goodsSmallImgCon {
  margin-top: 10px;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.prev {
  position: absolute;
  left: 3%;
  top: 50%;
  display: inline-block;
  border-left: 2px solid #ccc;
  border-bottom: 2px solid #ccc;
  width: 8px;
  height: 8px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.next {
  position: absolute;
  right: 3%;
  top: 50%;
  display: inline-block;
  border-left: 2px solid #ccc;
  border-bottom: 2px solid #ccc;
  width: 8px;
  height: 8px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-135deg);
}

/* 右边的部分 */
.goodsMsg {
  margin-top: 60px;
  font-size: 24px;
  color: #333;
}

.goodsPrice {
  font-size: 14px;
  color: #666;
}

.goodsPrice span {
  font-size: 24px;
  margin-left: 10px;
  color: #f36523;
}
.goodsPrice span i {
  font-size: 16px;
}

.goodsCity span {
  margin-left: 10px;
  color: #333;
}
.goodsDate span {
  margin-left: 10px;
  color: #333;
}

.conversation {
  width: 160px;
  margin-top: 20px;
  height: 48px;
  background: rgba(243, 101, 35, 1);
  border-radius: 2px;
  color: #fff;
}

.goodsMessage {
  padding: 30px 0;
  border-bottom: 1px solid rgba(236, 236, 236, 1);
}
.companyMessage {
  padding: 30px 0;
}

.title {
  color: #f36523;
  font-size: 20px;
}
.goodsIntro li {
  width: 100%;
  margin: 10px 0;
  overflow: hidden;
}
.goodsIntro li span {
  width: 100px;
  float: left;
  color: #666666;
  text-align: left;
}

.goodsIntro li p {
  float: left;
  width: 90%;
}

.dialogs {
  color: #666;
}
.numbers {
  margin-top: 30px;
}
.numbers,
.price,
.contacts,
.phone {
  margin-bottom: 10px;
}

.numbers span,
.price span,
.contacts span,
.phone span {
  display: inline-block;
  width: 170px;
  height: 36px;
  line-height: 36px;
  text-align: right;
}

.numbers input,
.price input,
.contacts input,
.phone input {
  border-radius: 3px;
  width: 257px;
  height: 26px;
  padding: 5px;
  line-height: 36px;
  border: 1px solid #ccc;
}

.btn {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-top: 40px;
}

.btn input {
  width: 125px;
  height: 36px;
  background: #f36523;
  border-radius: 2px;
  color: #fff;
}

el-message-box__btns {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-top: 40px;
}

.dialogsBtn {
  width: 125px;
  height: 36px;
  background: #f36523;
  border-radius: 2px;
  color: #fff;
  border: none;
}
.dialogsBtn:hover {
  background: #f36523;
}
.el-message-box__btns {
  padding: 0;
  margin: 0;
}

.el-button--primary:focus,
.el-button--primary:hover {
  background: #f36523;
}
.el-button--primary {
  color: #fff;
  background-color: #f36523 !important;
  border-color: #f36523 !important;
}

.el-input--suffix {
  width: 247px;
}
.el-input--suffix .el-input__inner {
  padding-right: 0px;
}
.el-message-box__btns {
  text-align: center;
}
.el-message-box {
  width: 535px !important;
  overflow: hidden !important;
  padding: 10px 0 35px !important;
}
.el-message-box__headerbtn {
  width: 30px;
  height: 30px;
  z-index: 2;
}
.smallTitle {
  padding-bottom: 10px;
}
.el-message-box__message em {
  color: red;
  position: relative;
  left: -6px;
  top: 3px;
}
</style>



