<template>
<div class="tabbar ">
  <div class="clearfix w1180">
    <div class="fl">
      <img class="fl"
           v-bind:src="imgUrl" />
      <span class="fl tit">中国首个成品油国际贸易平台</span>
    </div>
    <div class="fr">
      <ul class="fl">
        <li v-for="(item,index) in datas"
            @click="toggle(index)"
            :class="{'active':index ==checkindex }"
            class="pointer">
          {{item}}
        </li>
        <li>
          <a :href="information"
             target="_blank">信息资讯</a>
        </li>
        <li>
          <a :href="encyclopedias"
             target="_blank">油品百科</a>
        </li>
      </ul>
    </div>
  </div>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'tabBar',
  data () {
    return {
      imgUrl: '/static/oilbank.png',
      datas: ['首页', '油品大厅'],
      checkindex: 0,
      information: url.information,
      encyclopedias: url.encyclopedias
    }
  },
  components: {
  },
  watch: {
  },
  created () {
    window.home = this
    if (this.$route.path == '/home' || this.$route.path == '/') {
      this.checkindex = 0
    } else {
      this.checkindex = 1
    }

  },
  methods: {
    toggle (index) {
      this.checkindex = index
      if (index == 0) {
        this.$router.push({ path: '/home' })
      } else if (index == 1) {
        this.$router.push({ path: '/hall' })
      }
    }
  },
  mounted () {

  }
}
</script>

<style  scoped>
.tabbar {
  line-height: 100px;
  border-bottom:1px solid rgba(236,236,236,1); 
}
.tabbar img {
  margin: 26px 20px 0 0;
}
.tabbar ul li {
  float: left;
  font-size: 16px;
  padding: 0 12px 0 76px;
}
.tit {
  font-size: 18px;
  padding-right: 100px;
}
.active {
  color: #f36523;
}
</style>



